import React from "react"
import { connect } from "react-redux"
import Well from "../../components/budget/Well"
import Input from "../../components/budget/Input"
import * as Tools from "../../models/budget/Tools"
import Table from "../../components/budget/Table"
import Chart from "../../components/budget/Chart"
import {
  getArray,
  STRUCTURE_ID,
  STRUCTURE_HEADINGS,
} from "../../models/budget/tables"
import { planTypes } from "../../types/budget/types"

const Plan = ({ portfolio, setup }) => {
  let portfolioDetails = Tools.getAccounts(portfolio, STRUCTURE_HEADINGS)
  portfolioDetails = ["Click to Select Account"].concat(portfolioDetails)

  const STRUCTURE_INPUTS = (i, ii, iii, iiii) => [
    <Input
      id={STRUCTURE_ID}
      index={i}
      type={"select"}
      name={"Source"}
      placeholder={portfolioDetails}
    />,
    <Input
      id={STRUCTURE_ID}
      index={ii}
      type={"select"}
      name={"Target"}
      placeholder={portfolioDetails}
    />,
    <Input
      id={STRUCTURE_ID}
      index={iii}
      type={"number"}
      name={"Value"}
      placeholder={["Opening Balance Contribution"]}
    />,
    <Input
      id={STRUCTURE_ID}
      index={iiii}
      type={"date"}
      name={"Date"}
      placeholder={[Tools.getDate()]}
    />,
  ]

  const STRUCTURE_ARRAY = getArray(STRUCTURE_ID, STRUCTURE_INPUTS, setup.length)

  return (
    <>
      <Well>
        <Chart type="accounts" index={0} details="Accounts Balance" />
        <h4>SetUp:</h4>
        <Table headings={STRUCTURE_HEADINGS} inputs={STRUCTURE_ARRAY} />
      </Well>
    </>
  )
}

Plan.propTypes = planTypes

const mapStateToProps = ({ setup, portfolio }) => {
  return { setup, portfolio }
}
// const mapDispatchToProps = dispatch => {
//   return { clear: text => dispatch(resizeSession(text)) }
// }

// const ConnectedPlan = connect(mapStateToProps, mapDispatchToProps)(Plan)
const ConnectedPlan = connect(mapStateToProps, null)(Plan)

export default ConnectedPlan
