import React from "react"
import { connect } from "react-redux"
import Well from "../../components/budget/Well"
import Input from "../../components/budget/Input"
import Table from "../../components/budget/Table"

import {
  DEFAULT_BANKS,
  DEFAULT_COMPANIES,
  getArray,
  ACCOUNTS_ID,
  INCOME_ID,
  ACCOUNTS_HEADINGS,
  INCOME_HEADINGS,
  ACCOUNTS_OPTIONS_1,
  ACCOUNTS_OPTIONS_2,
  INCOME_OPTIONS_3,
} from "../../models/budget/tables"
import { profileTypes } from "../../types/budget/types"

const Profile = ({ portfolio, calculator }) => {
  const ACCOUNTS_INPUTS = (i, ii, iii, iiii) => [
    <Input
      id={ACCOUNTS_ID}
      index={i}
      type={"text"}
      name={ACCOUNTS_HEADINGS[0]}
      placeholder={DEFAULT_BANKS.concat(DEFAULT_COMPANIES)}
    />,
    <Input
      id={ACCOUNTS_ID}
      index={ii}
      type={"text"}
      name={ACCOUNTS_HEADINGS[1]}
      placeholder={ACCOUNTS_OPTIONS_1}
    />,
    <Input
      id={ACCOUNTS_ID}
      index={iii}
      type={"text"}
      name={ACCOUNTS_HEADINGS[2]}
      placeholder={ACCOUNTS_OPTIONS_2}
    />,
    <Input
      id={ACCOUNTS_ID}
      index={iiii}
      type={"number"}
      name={ACCOUNTS_HEADINGS[3]}
      placeholder={["Closing Balance"]}
    />,
  ]

  const ACCOUNTS_ARRAY = getArray(
    ACCOUNTS_ID,
    ACCOUNTS_INPUTS,
    portfolio.length
  )

  const INCOME_INPUTS = (i, ii, iii, iiii) => [
    <Input id={INCOME_ID} index={i} type={"date"} name={INCOME_HEADINGS[0]} />,
    <Input id={INCOME_ID} index={ii} type={"date"} name={INCOME_HEADINGS[1]} />,
    <Input
      id={INCOME_ID}
      index={iii}
      type={"number"}
      name={INCOME_HEADINGS[2]}
      placeholder={["Income/Deduction"]}
    />,
    <Input
      id={INCOME_ID}
      index={iiii}
      type={"select"}
      name={INCOME_HEADINGS[3]}
      placeholder={INCOME_OPTIONS_3}
    />,
  ]

  const INCOME_ARRAY = getArray(INCOME_ID, INCOME_INPUTS, calculator.length)

  const isBlank = value => {
    return (
      value === null ||
      value === undefined ||
      value === 0 ||
      value.toString().trim() === "" ||
      value.toString().trim() === "Click to SELECT Type"
    )
  }

  const include = index => {
    return !(
      isBlank(calculator[index + 1]) ||
      isBlank(calculator[index]) ||
      isBlank(calculator[index - 1]) ||
      isBlank(calculator[index - 2])
    )
  }

  let income = 0
  let deductions = 0

  const filter = () => {
    for (let i = 2; i < calculator.length; i += 4) {
      if (include(i)) {
        if (
          ["Employment (e.g. salary)", "Other Income"].indexOf(
            calculator[i + 1]
          ) === -1
        ) {
          deductions += parseFloat(calculator[i])
        } else income += parseFloat(calculator[i])
      }
    }
  }
  filter()

  const asSterling = value =>
    new Intl.NumberFormat("en-US", {
      style: "currency",
      currency: "GBP",
    }).format(value)
  const gIncome = asSterling(income)
  const nIncome = asSterling(income - deductions)

  const getTax = income => {
    let personalAllowanceLimit = 12500,
      basicRateLimit = 50000,
      higherRateLimit = 150000,
      basicRateAmount,
      higherRateAmount,
      additionalRateAmount,
      basicRate = 0.2,
      higherRate = 0.4,
      additionalRate = 0.45
    if (income <= personalAllowanceLimit) return 0
    if (income >= personalAllowanceLimit + 1 && income <= basicRateLimit) {
      basicRateAmount = (income - personalAllowanceLimit + 1) * basicRate
      return basicRateAmount
    }
    if (income > 120000) {
      personalAllowanceLimit = 0
    } else {
      personalAllowanceLimit -= (income - 100000) / 2
    }
    if (income >= basicRateLimit + 1 && income <= higherRateLimit) {
      basicRateAmount =
        (basicRateLimit - personalAllowanceLimit + 1) * basicRate
      higherRateAmount = (income - basicRateLimit + 1) * higherRate
      return basicRateAmount + higherRateAmount
    }
    if (income > higherRateLimit) {
      basicRateAmount =
        (basicRateLimit - personalAllowanceLimit + 1) * basicRate
      higherRateAmount = (higherRateLimit - basicRateLimit + 1) * higherRate
      additionalRateAmount = (income - higherRateLimit + 1) * additionalRate
      return basicRateAmount + higherRateAmount + additionalRateAmount
    }
  }

  const iTax = asSterling(getTax(income))

  const getnInsurance = income => {
    let amount,
      lowerEarningsLimit,
      primaryThreshold = 9504,
      secondaryThreshold,
      UpperEarningsLimit = 50004
    if (income <= primaryThreshold) return 0
    if (income > primaryThreshold && income <= UpperEarningsLimit)
      return (income - primaryThreshold) * 0.12
    if (income > UpperEarningsLimit) {
      amount = (UpperEarningsLimit - primaryThreshold) * 0.12
      amount += (income - UpperEarningsLimit) * 0.02
      return amount
    }
  }

  const nInsurance = asSterling(getnInsurance(income))

  const getULoan1 = income => {
    if (income > 19380) return (income - 19390) * 0.09
    return 0
  }

  const getULoan2 = income => {
    if (income > 26568) return (income - 26568) * 0.09
    return 0
  }

  const uLoan1 = asSterling(getULoan1(income))
  const uLoan2 = asSterling(getULoan2(income))

  const getPLoan = income => {
    if (income > 21000) return (income - 21000) * 0.09
    return 0
  }

  const pLoan = asSterling(getPLoan(income))

  return (
    <>
      <Well>
        <div>
          <h5>Calculator:</h5>
          <Table headings={INCOME_HEADINGS} inputs={INCOME_ARRAY} />
          <output name="income" htmlFor="">
            <details>
              <summary>
                Need help with Income Tax, National Insurance, and Student Loan
                Repayment calculations?
              </summary>
              <p>Income Tax Estimation = {iTax}</p>
              <p>National Insurance Estimation = {nInsurance}</p>
              <p>Undergraduate Plan 1 Loan Estimation = {uLoan1}</p>
              <p>Undergraduate Plan 2 Loan Estimation = {uLoan2}</p>
              <p>Postgraduate Loan Estimation = {pLoan}</p>
            </details>
            <p>Gross Income = {gIncome}</p>
            <p>Net Income = {nIncome}</p>
          </output>
        </div>
      </Well>
      <Well>
        <h5>Portfolio:</h5>
        <Table headings={ACCOUNTS_HEADINGS} inputs={ACCOUNTS_ARRAY} />
      </Well>
    </>
  )
}

Profile.propTypes = profileTypes

const mapStateToProps = ({ portfolio, calculator }) => {
  return { portfolio, calculator }
}
// const mapDispatchToProps = dispatch => {
//   return { clear: text => dispatch(resizeSession(text)) }
// }

// const ConnectedPlan = connect(mapStateToProps, mapDispatchToProps)(Plan)
const ConnectedProfile = connect(mapStateToProps, null)(Profile)

export default ConnectedProfile
