import React, { useState } from "react"
import { connect } from "react-redux"
import Well from "./Well"
import Button from "./Button"
import Switch from "./Switch"
import Input from "./Input"

import styles from "../../styles/budget/tools.module.scss"

import { updateSession, clear } from "../../state/actionTypes"

import {
  DEFAULT_CALCULATOR,
  DEFAULT_ANALYSIS,
  DEFAULT_PORTFOLIO,
  DEFAULT_SETUP,
  DEFAULT_TRANSACTIONS,
} from "../../models/budget/tables"

import { clearButtonTypes } from "../../types/budget/types"

// START of CLEARBUTTON CLASS
const ClearButton = ({ clearAll }) => (
  <Button label="Clear" colour="red" handleClick={clearAll} />
)

ClearButton.propTypes = clearButtonTypes

const mapStateToProps = ({ value }) => {
  return { value }
}

const mapDispatchToProps = dispatch => {
  return {
    clearAll: () => {
      dispatch(updateSession(""))
      dispatch(clear())
    },
  }
}

const ConnectedClearButton = connect(
  mapStateToProps,
  mapDispatchToProps
)(ClearButton)

// START OF SETTINGS CLASS
const Settings = () => {
  const [currentSession, setCurrentSession] = useState("Hello World!")
  const [greenIDLabel, setGreenIDLabel] = useState("Load")

  const handleIDChange = e =>
    e.target.value !== currentSession
      ? setGreenIDLabel("Load")
      : setGreenIDLabel("Copy")

  const [ID, setID] = useState(
    <Input
      type={"text"}
      name={"Session ID"}
      placeholder={["Provide Session ID"]}
      eventHandler={handleIDChange}
    />
  )
  const [smartMode, setSmartMode] = useState(true)

  const handleSwitch = () => setSmartMode(!smartMode)

  const handleDumbClick = () => {
    if (smartMode === true) handleSwitch()
  }

  const handleSmartClick = () => {
    if (smartMode === false) handleSwitch()
  }

  return (
    <Well>
      <div className={styles.IDSwitchSeperator}>
        <div className={styles.IDInputSeperator}>
          <div className={styles.flexCenter}>
            <label htmlFor="Session ID">Session ID</label>
            {ID}
          </div>
          <div className={styles.flexCenter}>
            <Button label={greenIDLabel} colour="green" />
            <Button label="â†»" colour="orange" />
            <ConnectedClearButton />
          </div>
        </div>
        <div className={styles.flexCenter}>
          <Button label="ðŸ¤ª" colour="red" handleClick={handleDumbClick} />
          <Switch status={smartMode} handleSwitch={handleSwitch} />
          {/* Estimate Tax and NI*/}
          <Button label="ðŸ¤“" colour="green" handleClick={handleSmartClick} />
        </div>
      </div>
    </Well>
  )
}

export default Settings
